* {
    margin: 0;
    padding: 0;
}

.header > nav {
display: flex;
justify-content: space-between;
align-items: center;
padding: 0.5vw 5.5vw;
background-color: rgb(46, 46, 46);
}

.header > nav > h1 {
   color: rgb(0, 255, 34);

   font-size: 1.3rem;
    }
    .header > nav > h1 > a {
        color: rgb(0, 204, 255);
        
         }    

         .header > nav > ul {
           
            display: flex;
justify-content: space-between;
align-items: center;
gap: 1vw;
font-weight: 300;
text-decoration: none;
color: aquamarine;
             }   

  .header > nav > ul > li {
           cursor: pointer;
margin: 3px;
padding: 3px;

             }   

             .header > nav > ul > li:hover {
                background-color: rgba(18, 255, 255, 0.151) ;
                
     
                  }  